.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

body {
  min-height: 100vh;
}

body {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.drum-pad {
  width: 132px;
  height: 125px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  margin: 10px;
  font-size: 1em;
  background-color: #242527;
  color: #fff;
  border: 3px solid #f51800;
  border-radius: 100%;
}

.drum-pad-area {
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  width: 470px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
}

#display {
  margin-bottom: 40px;
}

/*use sass*/

#drum-machine {
  padding: 10px 20px 40px;
}

/* #Mobile
 ================================================== */
@media (max-width: 767px) {
  .drum-pad-area {
    width: 100%;
  }

  .drum-pad {
    width: 27%;
    height: 90px;
  }
}



